import React from "react";
import { Container, Row, Col, Button, Tab, Tabs, Card } from "react-bootstrap";
import crypto1 from "../../styles/assets/images/Crypto/crypto1.png";
import crypto2 from "../../styles/assets/images/Crypto/crypto2.svg";
import crypto3 from "../../styles/assets/images/Crypto/crypto3.png";
import crypto4 from "../../styles/assets/images/Crypto/crypto4.png";
import crypto5 from "../../styles/assets/images/Crypto/cryptoanim.png";
import { useEffect, useState } from "react";

import TalkToUs from "../../components/common/talk-to-us";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";

import Loading from "../../components/common/loading";
import { useForm } from "react-hook-form";
import { Country } from "country-state-city";
import { collection, addDoc } from "firebase/firestore/lite";
import { utils } from "../../core/helper";
import "animate.css";
const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string().label("Email").required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label().required("Required"),
  content: Yup.string().label("Content").required(),
});

const CryptoDevelopmentScreen = (props) => {
  const { db } = props;
  console.log("db::", db);

  const [loading, setLoading] = useState(false);
  const [talkToUsShow, setTalkToUsShow] = useState(false);

  const [showImage1, setShowImage1] = useState(true);
  const [showImage2, setShowImage2] = useState(false);
  const delay = 1000;

  useEffect(() => {
    const intervalId = setInterval(() => {
      setShowImage1((prevShowImage1) => !prevShowImage1);
      setShowImage2((prevShowImage2) => !prevShowImage2);
    }, delay);

    return () => clearInterval(intervalId);
  }, []);

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const handleToggleTalkToUs = () => {
    setTalkToUsShow(!talkToUsShow);
    reset();
  };

  const countries = Country.getAllCountries();

  const handleOnSubmit = async (values) => {
    const { name, email, country, content, mobile } = values;
    const payload = {
      name,
      email,
      country,
      content,
      mobile,
      date: new Date(),
    };
    setLoading(true);
    try {
      const clients = collection(db, "clients");
      await addDoc(clients, payload);
      utils.showSuccssMsg("We will contact/talk to you soon");
      reset();
      setLoading(false);
      setTimeout(() => {
        handleToggleTalkToUs();
      }, 1000);
    } catch (e) {
      utils.showErrMsg("Error adding document");
      console.log("e::", e);
      setLoading(false);
    }
  };

  return (
    <Container>
      <Row>
        <Col>
          <div className="crypto-page">
            <section>
              <Row className="crypto-custom-container">
                <Col md={6} sm={12} className="px-5">
                  <h1>Cryptocurrency Exchange Development Services</h1>
                  <p>
                    Step into the future by launching a groundbreaking
                    cryptocurrency exchange brimming with features. Harness
                    Justtry's expertise in crypto exchange development today and
                    dominate the cryptocurrency realm.
                  </p>
                  <Button onClick={handleToggleTalkToUs}>TALK TO US</Button>
                </Col>
                <Col md={6} sm={12}>
                  <img
                    className="w-100 "
                    src={crypto1}
                    alt="Crypto Exchange images"
                  />
                </Col>
              </Row>
            </section>
            <section>
              <Row className="crypto-custom-container g-5 d-flex align-items-center">
                <Col
                  md={4}
                  sm={12}
                  className="d-flex align-items-center justify-content-center"
                >
                  <img
                    className="bitcoin w-75"
                    src={crypto2}
                    alt="Crypto Exchange images"
                  />
                </Col>
                <Col md={8} sm={12} className="px-5">
                  <h2>
                    Cryptocurrency Exchange Development: A Concise Overview
                  </h2>
                  <p>
                    Although the history of cryptocurrencies dates way back to
                    the 1980s, when they were known as cyber currencies, these
                    digital currencies gained popularity only after the launch
                    of Bitcoin in 2009. Over the years, the acceptance of
                    cryptocurrencies has only grown, with many financing
                    cryptocurrency-based projects. But what are these platforms?
                    Crypto exchanges are digital marketplaces that allow users
                    to buy, sell, or trade their digital currencies as well as
                    assets.
                  </p>
                  <p>
                    There are more than 235 cryptocurrency exchanges available
                    today, and each one offers a unique set of features and
                    functionalities. Due to the soaring demand and popularity of
                    crypto exchanges, entrepreneurs, startups, and even large
                    organizations have started to invest in cryptocurrency
                    exchange development projects. This is where we come in. As
                    one of the leading crypto exchange development companies
                    with expert minds on our team, Justtry can help you soar to
                    the top of the crypto market and generate revenue like never
                    before.
                  </p>
                </Col>
              </Row>
            </section>
            <section>
              <Row className="crypto-custom-container align-items-center">
                <Col md={8} sm={12} className="px-5">
                  <h2>Why Should You Invest in Crypto Exchange Development?</h2>
                  <p>
                    The crypto industry has grown by leaps and bounds in the
                    past few years. The mainstream acceptance of crypto-based
                    platforms has skyrocketed the demand for cryptocurrency
                    exchange apps. With G20 leaders deciding to swiftly
                    implement the Crypto Asset Reporting Framework (CARF), one
                    can clearly conclude that cryptocurrencies as well as
                    crypto-powered platforms will remain intact for the time
                    being. Binance, the largest cryptocurrency exchange to date,
                    saw its revenue grow 10 times in the past two years. The
                    fact that crypto exchange platforms not only survived the
                    bear market but also emerged profitable serves as strong
                    motivation to develop and launch a feature-packed
                    cryptocurrency exchange app today.
                  </p>
                </Col>
                <Col md={4} sm={12} className="d-flex justify-content-center">
                  {showImage1 && <img src={crypto3} alt="Crypto Man Images" />}
                  {showImage2 && <img src={crypto5} alt="Crypto Man Images" />}
                </Col>
              </Row>
            </section>
            <section>
              <Row className="crypto-custom-container-both  d-flex align-items-center justify-content-center">
                <Col md={10} sm={12} xs={12}>
                  <div className="crypto-custom-container-both">
                    <div className="content-btn-type">
                      <h3>
                        Join the Crypto Craze with Our State-of-the-Art
                        Cryptocurrency Exchange Today!
                      </h3>
                      <div className="demo-btn d-flex justify-content-between">
                        <h3 onClick={handleToggleTalkToUs}>
                          Get Demo with our Experts!
                        </h3>
                      </div>
                    </div>
                  </div>
                </Col>
              </Row>
            </section>

            <section>
              <Row className="crypto-custom-container-both py-5">
                <Col md={12} className="px-5">
                  <h2 className="text-center">
                    Various Approaches To Building a Sensational Crypto Exchange
                  </h2>
                  <p>
                    To meet the surging necessity for cutting-edge
                    cryptocurrency exchange platforms, numerous strategies came
                    into existence to build and launch these crypto exchange
                    softwares into the global market as soon as possible. Let’s
                    look at these one by one. 
                  </p>
                </Col>
              </Row>

              <Row className="crypto-custom-container-both gy-3 ">
                <Col md={4} lg={4}>
                  <div className="number-wrapper">
                    <Card className="crypto-cards">
                      <Card.Body>
                        <Card.Title>
                          <h3>Developing Crypto Exchange From Scratch</h3>
                        </Card.Title>
                        <Card.Text className="text-start">
                          This approach involves building the whole platform
                          from the ground up. From ideation to coding to launch,
                          every process is carried out step-by-step. This
                          strategy, although it helps you build your dream
                          cryptocurrency exchange app, might prove costly and
                          time-consuming in the long run. If you want to launch
                          your platform in a matter of months, you can go ahead
                          with this approach.
                        </Card.Text>
                      </Card.Body>
                      <div className="number-one">
                        <h2>01</h2>
                      </div>
                    </Card>
                  </div>
                </Col>
                <Col md={4} lg={4}>
                  <div className="number-wrapper">
                    <Card className="crypto-cards">
                      <Card.Body>
                        <Card.Title>
                          <h3>
                            Building Cryptocurrency Exchanges Via Clone Script
                          </h3>
                        </Card.Title>
                        <Card.Text className="text-start">
                          Clone scripts are nothing but a pre-built version of
                          existing cryptocurrency exchanges like Binance. These
                          are ready-to-launch and can be modified as per your
                          requirements. The best thing about using these clone
                          scripts is that you can launch your crypto exchange
                          app in a matter of weeks and start generating revenue.
                        </Card.Text>
                      </Card.Body>
                      <div className="number-one">
                        <h2>02</h2>
                      </div>
                    </Card>
                  </div>
                </Col>
                <Col md={4} lg={4}>
                  <div className="number-wrapper">
                    <Card className="crypto-cards">
                      <Card.Body>
                        <Card.Title>
                          <h3>Utilizing a White Label Solution</h3>
                        </Card.Title>
                        <Card.Text className="text-start">
                          A white label solution is readily available software
                          developed by one company but sold by another company
                          as your own. These, too, can be modified on the go and
                          launched in a couple of weeks. These are quite similar
                          to clone scripts, although you can advertise the
                          white-label crypto exchange as your own product as
                          opposed to clone scripts.
                        </Card.Text>
                      </Card.Body>
                      <div className="number-one">
                        <h2>03</h2>
                      </div>
                    </Card>
                  </div>
                </Col>
              </Row>
            </section>
            <section>
              <Row className="d-flex align-items-center justify-content-center ">
                <Col md={12} sm={10} xs={10}>
                  <h2 className="text-center mt-5">
                    Two Pioneering Services Offered by Our Cryptocurrency
                    Exchange
                  </h2>
                  <div className="trade-tab crypto-custom-container">
                    <Tabs
                      defaultActiveKey="Spot"
                      id="justify-tab-example"
                      className="mb-3"
                      justify
                    >
                      <Tab eventKey="Spot" title="Spot-Trading">
                        <p>
                          When it comes to spot trading, users buy
                          cryptocurrencies and wait for their price to rise
                          before selling them. The best thing about spot trading
                          is that you buy cryptocurrencies with your own money.
                          You can only buy what you can afford. This is why spot
                          trading is often considered safer when compared to
                          other trading markets. Also, the transactions between
                          traders are settled ‘on the spot’, hence the name. The
                          ultimate goal of spot trading is to buy assets at a
                          certain price and sell them once the price rises.
                          That’s why users might hold assets for numerous years.
                          Often, traders wait for the bull market to sell their
                          assets. Spot markets also consist of buyers, sellers,
                          and order books. Buyers post an order with a certain
                          bid or purchase price, and sellers place an order with
                          a specified ask or sale price for any cryptocurrency.
                        </p>
                      </Tab>
                      <Tab eventKey="p2p" title="Peer-2-Peer (P2P) Trading">
                        <p>
                          P2P trading allows users to trade directly with one
                          another  without any intermediaries. This feature
                          gives traders the freedom to choose a trader of their
                          choice. A user looking to buy cryptocurrency can check
                          out the seller’s list and choose the one that aligns
                          with his or her needs. This is what makes P2P trading
                          appealing to traders, as it is quite straightforward.
                          When compared to other traditional exchanges, P2P
                          platforms offer elevated privacy and control. Since
                          there is no intermediary involved, it results in
                          reduced or eliminated transaction fees. Our exchange
                          leverages blockchain technology to facilitate
                          transactions. Transactions are initiated by users by
                          generating offers or by accepting existing offers. An
                          automated escrow system holds the digital currencies
                          until both traders involved fulfill their
                          responsibilities. Anyone with an internet connection
                          can participate in P2P trading.
                        </p>
                      </Tab>
                    </Tabs>
                  </div>
                </Col>
              </Row>
            </section>
            <section>
              <Row className="crypto-custom-container-both d-flex align-items-center coincrypto">
                <Col md={8} sm={12} className="px-5">
                  <h2>Why Justtry for Cryptocurrency Exchange Development?</h2>
                  <p>
                    Since 2020, Justtry has worked with several clients all
                    across the world, allowing us to gain insight into their
                    needs. Our skilled development team strives to create
                    high-quality products with cutting-edge features. Our
                    cryptocurrency exchanges are incorporated with
                    next-generation technologies that will help you thrive in
                    the global market in the future. Leverage our cryptocurrency
                    exchange development services and launch yourself into the
                    global crypto market. Get in touch with us today to
                    transform your valuable vision into a spectacular reality.
                    We offer:
                  </p>
                  <ul className="currency">
                    <li>In-House Team of Experts</li>
                    <li>Bug-Free Solutions</li>
                    <li>ound-the-Clock Support</li>
                    <li>Intuitive UI/UX</li>
                    <li>Quality Assurance</li>
                  </ul>
                </Col>
                <Col md={4}>
                  <img
                    className=" coin-man w-100"
                    src={crypto4}
                    alt="Crypto Exchange images"
                  />
                </Col>
              </Row>
            </section>
            <section>
              <Row className="crypto-custom-container-both">
                <div id="ctus">
                  <p className="text-center">Ready to get Started? </p>
                  <h1 className="text-center">
                    Get started with the Customer<br></br> Success Center
                  </h1>
                  <button
                    className="text-center"
                    id="ctus-button"
                    onClick={handleToggleTalkToUs}
                  >
                    Let's Connect
                  </button>
                </div>
              </Row>
            </section>
          </div>
          <TalkToUs
            show={talkToUsShow}
            onHide={handleToggleTalkToUs}
            closeButton={true}
            size={"lg"}
          >
            <div class="screen__content">
              <form onSubmit={handleSubmit(handleOnSubmit)} class="login">
                <div class="login__field">
                  <i class="login__icon fas fa-user"></i>
                  <input
                    type="text"
                    class="login__input"
                    {...register("name")}
                    placeholder="Your Name"
                  />
                  <p className="text-danger mb-0">{errors.name?.message}</p>
                </div>
                <div class="login__field">
                  <i class="login__icon fas fa-lock"></i>
                  <input
                    type="email"
                    class="login__input"
                    {...register("email")}
                    placeholder="Email"
                  />
                  <p className="text-danger mb-0">{errors.email?.message}</p>
                </div>
                <div class="login__field">
                  <i class="login__icon fas fa-lock"></i>
                  <input
                    type="text"
                    class="login__input"
                    placeholder="Skype / Mobile"
                    {...register("mobile")}
                  />
                  <p className="text-danger mb-0">{errors.mobile?.message}</p>
                </div>
                <div class="login__field">
                  <i class="login__icon fas fa-lock"></i>
                  <select class="login__input" {...register("country")}>
                    <option value="">Select</option>
                    {countries.map((ele, ind) => {
                      return (
                        <option key={ind} value={ele.isoCode}>
                          {ele.name}
                        </option>
                      );
                    })}
                  </select>
                  <p className="text-danger mb-0">{errors.country?.message}</p>
                </div>
                <div class="login__field">
                  <i class="login__icon fas fa-lock"></i>
                  <textarea
                    id="content"
                    placeholder="Type content here"
                    rows={5}
                    className="login__input"
                    {...register("content")}
                    maxLength={200}
                  ></textarea>
                  <p className="text-danger mb-0">{errors.content?.message}</p>
                </div>
                <button disabled={loading} class="button login__submit">
                  <span class="button__text">
                    {" "}
                    Submit {loading && <Loading />}
                  </span>
                  <i class="button__icon fas fa-chevron-right"></i>
                </button>
              </form>
            </div>
            <div class="screen__background">
              <span class="screen__background__shape screen__background__shape4"></span>
              <span class="screen__background__shape screen__background__shape3"></span>
              <span class="screen__background__shape screen__background__shape2"></span>
              <span class="screen__background__shape screen__background__shape1"></span>
            </div>
          </TalkToUs>
        </Col>
      </Row>
    </Container>
  );
};

export default CryptoDevelopmentScreen;
